/* Base */
body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background-color: #0f0a30;
  color: white;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #1a1446;
  padding: 12px 20px;
  border-bottom: 2px solid #2b2b70;
  z-index: 100;
}

.header-left {
  display: flex;
  align-items: center;
}

.header-title {
  font-size: 1.5rem;
  font-weight: 600;
}

.login-btn {
  background-color: #5b4bff;
  color: white;
  border: none;
  padding: 8px 18px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.login-btn:hover {
  background-color: #3d34c0;
}

/* Hamburger button */
.hamburger {
  display: none;
  font-size: 1.8rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  margin-right: 10px;
}

/* Main layout */
.main-content {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.sidebar {
  width: 250px;
  background-color: #1e1e1e;
  color: white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  border-right: 1px solid #333;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background: #252526;
  border-bottom: 1px solid #333;
  position: sticky;
  top: 0;
  z-index: 10;
}

.sidebar-header h3 {
  font-size: 14px;
  margin: 0;
}

.sidebar-actions button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  margin-left: 5px;
}

.sidebar-actions button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* âœ… Scrollable region */
.file-tree-scroll {
  flex: 1;
  overflow-y: auto;
  padding: 8px 0;
  scrollbar-width: thin;
  scrollbar-color: #555 #1e1e1e;
}

.file-tree-scroll::-webkit-scrollbar {
  width: 8px;
}

.file-tree-scroll::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 4px;
}

.file-tree-scroll::-webkit-scrollbar-thumb:hover {
  background: #777;
}

/* Tree item styling */
.tree-item {
  padding: 3px 12px;
  border-radius: 4px;
  transition: background 0.2s;
}

.tree-item:hover {
  background-color: #2a2d2e;
}

.tree-item.selected {
  background-color: #37373d;
  color: #fff;
}

/* File tree */
.tree-item {
  display: flex;
  align-items: center;
  padding: 4px 6px;
  border-radius: 4px;
  cursor: pointer;
  /* pointer for files/folders */
  transition: background 0.2s ease;
  user-select: none;
}

.tree-item:hover {
  background-color: #3d34c0;
}

.tree-item.active {
  background-color: #5b4bff;
}

/* Nested indentation */
.tree-item.level-1 {
  padding-left: 15px;
}

.tree-item.level-2 {
  padding-left: 30px;
}

.tree-item.level-3 {
  padding-left: 45px;
}

/* Editor & Preview */
.editor-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.code-editor,
.live-preview {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 50%;
}

.code-editor {
  background: #0f0a30;
  padding: 10px;
  border-right: 2px solid #2b2b70;
  width: 50%;
}

.code-editor textarea {
  background: #1a1446;
  color: #fff;
  resize: none;
  padding: 10px;
  border-radius: 4px;
  font-family: monospace;
  font-size: 14px;
}

.code-editor textarea:focus {
  outline: 2px solid #5b4bff;
}

.live-preview {
  background: #0f0a19;
  padding: 10px;
}

.live-preview iframe {
  flex: 1;
  border: gray;
  background: white;
  border-radius: 4px;
  width: 100%;
  height: 100%;
}

.preview-header {
  color: black;
}

/* Tabs */
.tabs {
  display: flex;
  background: #1f1a50;
  border-bottom: 1px solid #3d34c0;
}

.tab {
  padding: 5px 10px;
  margin-right: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  background: #2a2560;
  border-radius: 4px 4px 0 0;
  position: relative;
  transition: background 0.2s ease;
}

.tab:hover {
  background: #5b4bff;
}

.active-tab {
  background-color: #3d34c0;
}

.close-btn {
  margin-left: 8px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

.tree-item.selected {
  background-color: rgba(0, 128, 255, 0.15);
  border-left: 3px solid #007bff;
}

/* Tabs container */
.tabs {
  display: flex;
  background: #1f1a50;
  border-bottom: 1px solid #3d34c0;
  padding: 4px 8px;
  gap: 4px;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: #1f1e25 #1f1a50;
}

/* Each tab */
.tab {
  padding: 6px 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  background: #2a2560;
  border-radius: 6px 6px 0 0;
  position: relative;
  font-weight: 500;
  transition: all 0.2s ease;
}

/* Hover effect */
.tab:hover {
  background-color: #2d2c39;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

/* Active tab */
.active-tab {
  background-color: #000000;
  font-weight: 600;
  box-shadow: 0 0 8px rgba(125, 125, 125, 0.7);
  border-bottom: 2px solid #2d2323;
}

/* Close button */
.close-btn {
  margin-left: 8px;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
  padding: 0 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.close-btn:hover {
  color: #f70c0c;
}

/* Scrollbar for overflow */
.tabs::-webkit-scrollbar {
  height: 6px;
}

.tabs::-webkit-scrollbar-thumb {
  background: #000000;
  border-radius: 3px;
}

.tabs::-webkit-scrollbar-track {
  background: #1f1a50;
}

/* Overlay for mobile sidebar */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 15;
}

/* ===== Responsive ===== */
@media (max-width: 768px) {
  .hamburger {
    display: inline-block;
  }

  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    z-index: 20;
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .main-content {
    flex-direction: column;
  }

  .editor-section {
    flex-direction: column;
  }

  .code-editor,
  .live-preview {
    border-left: none;
    border-top: 2px solid #2b2b70;
  }

}